import { mavonEditor } from "mavon-editor";
import "mavon-editor/dist/css/index.css";
export default {
  name: "article-edit",
  data() {
    return {
      ruleForm: {
        header_cover_url: "",
        title: "",
        summary: "",
        is_release: "0",
        handbook: ""
      },
      rules: {
        header_cover_url: [{ required: true, message: "" }],
        title: [{ required: true, message: "" }],
        summary: [{ required: true, message: "" }],
        handbook: [{ required: true, message: "" }]
      },
      markdownOption: {
        bold: true, // 
        italic: true, // 
        header: true, // 
        underline: true, // 
        strikethrough: true, // 
        mark: true, // 
        superscript: true, // 
        subscript: true, // 
        quote: true, // 
        ol: true, // 
        ul: true, // 
        link: true, // 
        imagelink: true, // 
        code: true, // code
        table: true, // 
        fullscreen: true, // 
        readmodel: true, // 
        htmlcode: true, // html
        help: true, // 
        /* 1.3.5 */
        undo: true, // 
        redo: true, // 
        trash: true, // 
        save: true, // eventssave
        /* 1.4.2 */
        navigation: true, // 
        /* 2.1.8 */
        alignleft: true, // 
        aligncenter: true, // 
        alignright: true, // 
        /* 2.2.1 */
        subfield: true, // 
        preview: true // 
      },
      dialogVisible: false,
      picAction: "",
      picData: {},
      fileList: []
    };
  },
  components: {
    mavonEditor
  },
  props: {},
  created() {
    this.getPolicy();
  },
  methods: {
    async getPolicy() {
      const res = await this.$http.get("/api/imagesPolicy/getPolicy", {});
      if (res.data && res.data.code == 0) {
        console.log(res.data);
        this.picAction = res.data.data.action;
        this.picData = {
          token: res.data.data.upload_token
        };
      }
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.postFn();
        } else {
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    handlePictureCardPreview() {
      this.dialogVisible = true;
    },
    handleRemove() {},
    handleError(err, file, fileList) {
      console.log("err", err, file, fileList);
    },
    handleSuccess(response, file, fileList) {
      console.log("suc", response, file, fileList);
      this.ruleForm.header_cover_url = response.url;
    },
    handleBefore(file) {
      console.log("before", file);
      this.$set(this.picData, "key", file.name);
    },
    async postFn() {
      const params = { ...this.ruleForm };
      const res = await this.$http.post("/api/save-articles", params);
      if (res.data && res.data.code == 0) {
        console.log(res.data);
        this.$message.success("");
      }
    }
  }
};
